import Vue from 'vue';
import lang from 'element-ui/lib/locale/lang/en';
import locale from 'element-ui/lib/locale';
import { Alert, Button, Select, Cascader,Checkbox, CheckboxButton, Dialog, DatePicker,Dropdown, DropdownMenu, DropdownItem,
    CheckboxGroup, Form, FormItem, Input, MessageBox,Notification, Option, Radio,RadioButton,RadioGroup,Row, Col, Popover,Table,TableColumn,
     Card,  Tooltip,TabPane, Tabs
} from 'element-ui';
locale.use(lang);
//import locale from 'element-ui/lib/locale/lang/en';

//Vue.use(ElementUI, { locale })
Vue.use(Alert)
Vue.use(Card)
Vue.use(Dialog)
Vue.use(Dropdown)
Vue.use(DatePicker ,{ locale })
Vue.use(DropdownItem)
Vue.use(DropdownItem)
Vue.use(Form)
Vue.use(FormItem)
//Vue.use(Notification)
//Vue.use(MessageBox)
Vue.use(Button)
Vue.use(Radio)
Vue.use(RadioButton)
Vue.use(RadioGroup)
Vue.use(Row)
Vue.use(Col)
Vue.use(Cascader)
Vue.use(Checkbox)
Vue.use(CheckboxGroup)
Vue.use(CheckboxButton)
Vue.use(Input)
Vue.use(Option)
Vue.use(Select)
Vue.use(Table)
Vue.use(Tooltip)
Vue.use(Tabs)
Vue.use(Popover)
Vue.use(TableColumn)
Vue.use(TabPane)

export default {
    Alert, Button, Card,Cascader, Checkbox, CheckboxButton, Dialog,Dropdown,DatePicker, DropdownMenu, DropdownItem,
    Input, CheckboxGroup, Form, FormItem, MessageBox, Option,Radio, RadioButton,RadioGroup, Row, Col, Popover,
    Notification,
     Table,TableColumn,
    Select,TabPane, Tooltip,Tabs
  };
  Vue.prototype.$notify = Notification;
  Vue.prototype.$alert = MessageBox.alert;


