/* ============
 * Routes File
 * ============
 *
 * The routes and redirects are defined in this file.
 */
import store from '../store';
const checkRequestsLoaded = (to, from, next) => {
  console.log("check requests selectedRequest", store.state.titleRequest.requestsLoaded )
  function proceed () {
    if (store.state.titleRequest.requestsLoaded) {
         console.log("check requests proceed with true" ,store.state.titleRequest.requestsLoaded )
      next()
    }
  }
  if (!store.state.titleRequest.requestsLoaded) {
    console.log("check requests watch" ,store.state.titleRequest.requestsLoaded )
    store.watch(
      (state) => state.titleRequest.requestsLoaded,
      (value) => {
        if (value === true) {
         console.log("check requests value true" , store.state.titleRequest.requestsLoaded );
          proceed();
        }
      }
    )
  } else {
    proceed()
  }
}
export default [
  // Home
  {
    path: '/home',
    name: 'home.index',
   // beforeEnter: checkRequestsLoaded,
    component: () => import('@/pages/Home/Index'),
    props:true,

    // If the user needs to be authenticated to view this page
    meta: {
      auth: false,
    },
  },

 

  // Login
  {
    path: '/login',
    name: 'login.index',
    component: () => import('@/pages/Login/Index'),

    // If the user needs to be a guest to view this page.
    meta: {
      guest: true,
    },
  },

  

  {
    path: '/',
    redirect: '/home',
  },

  {
    path: '/*',
    redirect: '/home',
  },
];



