/* ============
 * Slot Mixin
 * ============
 *
 * Mixins are used to easily expand Vue components.
 * This slot-mixin, will add the hasSlot-method
 * to the specified Vue component.
 */


export default {
  data() {
    return {
      // titleRequest:{
      //   Title:'',
      //    Author:'',
      //     Format:'',
      //      PatronEmail:'',
      // Barcode:'', 
      // Isbn:'', 
      // OclcNumber:'',
      //  Publisher:'',
      //  PublicationYear:'',
      //  PatronComments:'',
      //   Title:'',
      //    Author:'',
      // EmailTemplateSent:'',
      //  Status:'', 
      //  Season:'', 
      //  HoldPickupLocation:'1',
      // IsILLItem:'',
      //  EmailText:'',
      //   StaffComments:'',
      //    UserName:'',  
      //    Reference:'',
      //  ProcessStatus:'',
      //  PlaceHoldWhenAvailable:''},
    };
  },

 
  /**
   * The methods that the mixin will add.
   */
  methods: {
    /**
     * Method used to check if a Vue component has a slot.
     *
     * @param slotName The name of the slot.
     *
     * @returns {boolean} Whether the component has a slot.
     */
    openProspector() {
      const h = this.$createElement;
      this.$notify({
          title: 'Find it in Prospector',
          duration:9500,
          offset: 100,
          position: 'bottom-left',
          message: h('i', { style: 'color: teal' }, 
          'Did you know that the Prospector catalog makes finding non-DCL items ' +
          'fast and easy? The next time you can’t find an item in our catalog, ' +
           'give Prospector a try! You can find it at ' +
           'http://encore.coalliance.org/iii/encore/?lang=eng'
        )
      });
  },
  openSavedRequest() {
    const h = this.$createElement;
    this.$notify({
        title: 'Request Saved ',
        duration:5000,
        offset: 100,
        position: 'bottom-left',
        message: h('i', { style: 'color: teal' }, 
        'You can view your saved request in "Show My Request" tab ' 
      )
    });
},
    formatFormatter(row,column)
    {
     // console.log('formateer row coloum ',row, ' ',column)
      var d = 
      this.$formats.
           find(x => x.FormatId === row.Format).name;
      return  d ;
    },
    emailTemplateFormatter(row,column)
    {
    
      var templates = this.$store.state.titleRequest.requestConfigs.EmailTemplate;
      var d = "None";
      var foundit = false;
      templates.forEach(element => {
        if (element.EmailTemplateId == row.EmailTemplateSent * 1) {
          foundit = true;
         // console.log('foundit');
         // break;
        }
      });
      if (foundit) {
          d = 
          this.$store.state.titleRequest.requestConfigs.EmailTemplate.
             find(x => x.EmailTemplateId === row.EmailTemplateSent * 1 ).Name;
      }
    else{
     // console.log('EmailTemplateSent NOT FOUND ',row.EmailTemplateSent)
    }
      // var d = 
      //    this.$store.state.titleRequest.requestConfigs.EmailTemplate.
      //     find(x => x.EmailTemplateId === row.EmailTemplateSent).Name;
     // find(x => x.EmailTemplateId === row.EmailTemplateSent).Name;
      return  d ;
    },
    createdDateFormatter(row,column)
    {
     // console.log('formateer row coloum ',row, ' ',column)
      var d = new Date(row.CreatedTime);
      return  d.toLocaleString() ;
     // return row.CreatedTime +  'xxx';
    },
    statusFormatter(row,column)
    {
     // console.log('formateer row coloum ',row, ' ',column)
      var d = 
      this.$statuses.
           find(x => x.StatusId === row.Status).Name;
      return  d ;
     // return row.CreatedTime +  'xxx';
    },
    
    modifiedDateFormatter(row,column)
    {
      var d = new Date(row.ModifiedTime);
      return  d.toLocaleString() ;
    },
    isILLFormatter(row,column)
    {
      var d = row.isILL  ? "Yes" :"No";
      return  d;
    },
    handleCellClick(row, column, cell, event)
    {
      //console.log(' click frow, column, cell, event',row, column, cell, event)
     if (column.property === "EmailTemplateSent") {
      console.log(' click frow, column, cell, event',row.EmailTemplateSent, row.EmailText, )
     }
    },
  },
};



