import Vue from 'vue';
import store from '@/store';



export default {
  
  async saveTitleRequest(context, payload) {
   // context.commit('selectedChange', false)
   // context.commit('selectedRequestStatus',  context.state.selectedRequest.LastStatus);
  //  context.commit('selectedRequestFormat',  context.state.selectedRequest.LastFormat);
    console.log('save TitleRequests titleRequest ACTION', context, 'pay', payload);
   // process.env.API_BASE_URL
   // await Vue.$http.put(`http://localhost:3020/api/Requests/` +
    await Vue.$http.post(process.env.API_BASE_URL +`api/Requests/` ,
     
    payload).
      then(response => {
        // JSON responses are automatically parsed.
       // console.log('title request action save', context.state.titleRequests);
      //  console.log('title request action save response', PaymentResponse);
        //context.dispatch('getTitleRequests');
      })
      .catch(e => {
        //this.errors.push(e);
         console.log("got error", e);
      });
     return Promise.resolve();
     
  },

  async getPatron(context, payload) {
    console.log('getPatron titleRequest ACTION', context, 'pay', payload);
    // await context.dispatch('getTitleConfigs')
    var patronData = {};
    console.log('action pre commit trpst', patronData);
   // console.log('action pre commit trpst',  this.$patronData)
    patronData.Barcode = payload.id;
    patronData.Pin = payload.pin
    await Vue.$http.get(process.env.API_BASE_URL +`api/SIP?id=` +
     payload.id + "&pin=" + payload.pin ).
      then(response => {
        // JSON responses are automatically parsed.
        this.posts = response.data;
        patronData.Name = this.posts.Name;
        patronData.Email = this.posts.Email;
        patronData.Location = this.posts.Location;
        patronData.Authorized = this.posts.Authorized;
      //  var f = tran.find(x => x.FormatId === selected).Name;
        console.log('action post data ', this.posts);
        console.log('action pre patron data', patronData);
        context.commit('patronInfo', patronData);
        // context.commit('requestsLoaded', true);
         
       // this.$store.commit('titleRequests/titleRequests', this.posts);
       // console.log('action created posts trpst', this.posts);
       // console.log('title request action trstore', context.state.titleRequests);
      })
      .catch(e => {
       
        if(!e.response)
        {
          console.log("!e", e);
          patronData.Message = ' Likely a connection problem ';
        }
        else
        {
          console.log("got error", e);
          patronData.Message = e.response.data;
          console.log("got error", e.response.data);
        }
        patronData.Authorized = false;
        context.commit('patronInfo', patronData);
      });
     return Promise.resolve();
  },
  async getPatronRequests(context, payload) {
    console.log('getPatronRequests ACTION', context, 'pay', payload);
    // await context.dispatch('getTitleConfigs')
    var patronData = {};
    console.log('action pre commit trpst', patronData);
   // console.log('action pre commit trpst',  this.$patronData)
    patronData.Barcode = payload.id;
    patronData.Pin = payload.pin
    await Vue.$http.post(process.env.API_BASE_URL +`api/Requests/userRequests?barcode=` +
     payload ).
      then(response => {
        // JSON responses are automatically parsed.
        this.posts = response.data;
      //   patronData.Name = this.posts.Name;
      //   patronData.Email = this.posts.Email;
      //   patronData.Location = this.posts.Location;
      //   patronData.Authorized = this.posts.Authorized;
      // //  var f = tran.find(x => x.FormatId === selected).Name;
      //   console.log('action post data ', this.posts);
      //   console.log('action pre patron data', patronData);
      //   context.commit('patronInfo', patronData);
        // context.commit('requestsLoaded', true);
         
        context.commit('patronRequests', this.posts);
       // console.log('action created posts trpst', this.posts);
       // console.log('title request action trstore', context.state.titleRequests);
      })
      .catch(e => {
       
        if(!e.response)
        {
          console.log("!e", e);
         // patronData.Message = ' Likely a connection problem ';
        }
        else
        {
          console.log("got error", e);
        //  patronData.Message = e.response.data;
          console.log("got error", e.response.data);
        }
       // patronData.Authorized = false;
       // context.commit('patronInfo', patronData);
      });
     return Promise.resolve();
  },


};



