import RequestHelperMixin from '@/mixins/requestHelper';
import store from '@/store';
export default {
    patronLoginGetter(state,getters){
        console.log("state", state);
        console.log("patronLoginGetter", state.patronInfo);
        var x = JSON.parse(JSON.stringify(state.patronInfo));
        if (x.Location === "") {
            x.Location = {id:1, HorizonName:"pa", name:"Parker"};
        }
        console.log("x", x);
        return x;
       // return state.patronInfo;
    }

   // patronLoginGetter: state => { return JSON.parse(JSON.stringify(state.patronInfo)); }
   
};



