import { GET } from './mutation-types';

export default {
    
  patronLoaded(state,payload)
  {
    console.log('patronLoaded commit',state, payload);
    
    state.patronLoaded = payload;
  },
  patronInfo(state, payload)
  {
    console.log('patronInfo payload',payload)
    state.patronInfo = payload;
   
  },
  patronRequest(state, payload)
  {
    console.log('patronRequest payload',payload)
    state.patronRequest = payload;
   
  },
  patronRequests(state, payload)
  {
    console.log('patronRequestSSSS payload',payload)
    state.patronRequests = payload;
   
  },
  selectedTab(state, payload)
  {
    console.log('selectedTab payload',payload)
    state.selectedTab = payload;
   
  },
};



